import React, { FC, Suspense } from "react";

import { Logo, Icon } from "components";
import { useWindowSize } from "providers";

import LazyComponentLoader from "config/LazyComponentLoader";
import Footer from "pages/Onboarding/components/Footer";
import { Scrollbar } from "components/Scrollbar";
import { NewBanners } from "./components/NewBanners";

import { ContentProps } from "./types";
import css from "./style.module.scss";

const EventLogo = React.lazy(() =>
  LazyComponentLoader(
    import("containers/Page/components/AppBar/components/EventLogo"),
  ),
);

const Content: FC<ContentProps> = ({ activeEvent, setOpen }) => {
  const { height, width } = useWindowSize();

  const handleClose = () => {
    setOpen(false);
  };

  const scrollStyle = {
    bar: {
      width: width,
      height: height,
    },
    div: {
      minHeight: height,
    },
  };

  return (
    <Scrollbar
      style={scrollStyle.bar}
      containerProps={{ className: css.scrollContainer }}>
      <div className={css.innerWrapper} style={scrollStyle.div}>
        <div className={css.closeBlock}>
          <Icon
            variant="close"
            className={css.closeIcon}
            onClick={handleClose}
          />
        </div>
        <div className={css.header}>
          {activeEvent ? (
            <Suspense fallback={null}>
              <EventLogo
                eventName={activeEvent}
                className={activeEvent === "christmas" ? css.christmasLogo : ""}
              />
            </Suspense>
          ) : (
            <Logo
              className={css.logo}
              logoVariant={"pasino"}
              to={{ pathname: "/" }}
              onClick={handleClose}
            />
          )}
        </div>
        <div className={css.container}>
          <NewBanners onClose={handleClose} />
        </div>
        <Footer />
      </div>
    </Scrollbar>
  );
};

export default Content;
