import React, { useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { Typography } from "components";
import { useWindowSize } from "providers";

import { bannersConfig, BannerImageInterface } from "./config";

import css from "./style.module.scss";

const initialContainerWidth = parseInt(css.containerWidth);
const marginSize = parseInt(css.marginSize);
const containerWidth = initialContainerWidth - marginSize * 2;
const fontSizeQuotient = containerWidth / parseInt(css.desktopFontSize);
const lineHeightQuotient = containerWidth / parseInt(css.desktopLineHeight);

interface MapperProps {
  banners: BannerImageInterface[];
  onClick: () => void;
  className: string;
}
const BannersMapper = ({ banners, onClick, className }: MapperProps) => {
  return (
    <>
      {banners.map(({ id, title, imgUrl, link }) => (
        <Link key={id} to={link} className={className} onClick={onClick}>
          <img src={imgUrl} alt={title} />
        </Link>
      ))}
    </>
  );
};
interface BannersProps {
  onClose: () => void;
}

export const NewBanners = ({ onClose }: BannersProps) => {
  const titleRef = useRef<HTMLDivElement | null>(null);
  const { width, isMobile } = useWindowSize();
  const { bigBanners, originals } = bannersConfig(isMobile);

  useEffect(() => {
    if (isMobile) {
      return;
    }
    const wrapperWidth =
      width >= containerWidth ? containerWidth : width - marginSize * 2;

    const { children } = titleRef.current as HTMLDivElement;

    const titles = [...children] as HTMLAnchorElement[];
    const changeTitleStyles = (lineHeghtSize: string, fontSize: string) => {
      titles[0].style.lineHeight = lineHeghtSize;
      titles.forEach((title) => {
        title.style.fontSize = fontSize;
      });
    };

    changeTitleStyles(
      `${wrapperWidth / lineHeightQuotient}px`,
      `${wrapperWidth / fontSizeQuotient}px`,
    );
    return () => changeTitleStyles(css.mobileLineHeight, css.mobileFontSize);
  }, [isMobile, width]);

  return (
    <div className={css.container}>
      <div className={css.bigBanners}>
        <BannersMapper
          banners={bigBanners}
          className={css.bigBanner}
          onClick={onClose}
        />
      </div>
      <div className={css.originalsWrapper}>
        <div className={css.titleContainer} ref={titleRef}>
          <Typography className={css.title}>ORIGINAL</Typography>
          <Typography className={clsx(css.title, css.gamesLetter)}>
            GAMES
          </Typography>
        </div>
        <BannersMapper
          banners={originals}
          className={css.originalBanner}
          onClick={onClose}
        />
      </div>
    </div>
  );
};
