import React from "react";
import { useGetLocale } from "providers";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { Breadcrumbs } from "components";
import { useWhiteTheme } from "providers";

import css from "./footer.module.scss";

const Footer = () => {
  const intl = useGetLocale();
  const { activeEvent } = useWhiteTheme();

  return (
    <div className={clsx(css.footer, activeEvent && css[activeEvent])}>
      {intl.formatMessage({ id: "copyright" })}
      <Breadcrumbs
        className={css.breadcrumbs}
        elements={[
          <Link className={css.link} to="/terms">
            {intl.formatMessage({ id: "terms" })}
          </Link>,
          <Link className={css.link} to="/privacy">
            {intl.formatMessage({ id: "privacy" })}
          </Link>,
          <a
            className={css.link}
            href="https://www.responsiblegambling.org/for-the-public/safer-play/"
            rel="noreferrer">
            {intl.formatMessage({ id: "gambleAware" })}
          </a>,
        ]}
      />
    </div>
  );
};

export default Footer;
