export const bannersConfig = (isMobile: boolean) => {
  const bigBanners: BannerImageInterface[] = [
    {
      id: 0,
      title: "lottery",
      imgUrl: isMobile ? LotteryMobileURL : LotteryURL,
      link: "/lottery",
    },
    {
      id: 1,
      title: "marketplace",
      imgUrl: isMobile ? MarketPlaceMobileURL : MarketPlaceURL,
      link: "/marketplace",
    },
    {
      id: 2,
      title: "slot-games",
      imgUrl: isMobile ? SlotsMobileURL : SlotsURL,
      link: "/slot-games",
    },
    {
      id: 3,
      title: "live-games",
      imgUrl: isMobile ? LiveGamesMobileURL : LiveGamesURL,
      link: "/slot-games?category=live",
    },
  ];

  return { bigBanners, originals };
};
